
#BTNVisiteVirtuelle{
    font-family: "IBM_Light", sans-serif;
    color: white;
    width: max-content;
    height: max-content;
    cursor: pointer;
    text-decoration:none;
}
@media only screen and (min-width: 768px) {
    #BTNVisiteVirtuelle{
        border: 0.13vw white solid;
        border-radius: 10px;
        position: relative;
        padding: 0.5vh;
        font-size: 0.85vw;
    }
    #BTNVisiteVirtuelle:hover {
        transform: scale(1.1);
        transition: transform 0.5s;
        border: 0.12vw white solid;
    }

    #logo{
        z-index: 4;
        bottom: 1.5vh;
        right: 0;
        margin-right: 2vw;
        grid-gap: 1.2vw;
    }

    .SocialNetworkIcon_white:hover{
        transform: scale(1.1);
        transition: transform 0.5s;
    }

    #CentraleMarseille_white{
        height: 7vh;
    }
}
#logo{
    position: absolute;
    display: grid;
    grid-template-columns: max-content max-content max-content max-content;
    vertical-align: middle;
    align-items: center;
    width: max-content;
}
#wave{
    position: absolute;
    z-index: 1;
    height: 11vh;
    width: 100vw;
    bottom: 0;
}

.SocialNetworkIcon_white {
    height: 5vh;
    width: auto;

}

@media only screen and (max-width: 767px) {
    #Footer{
        overflow: hidden;
        width: 100%;
        height: auto;
        display: inline-block;
    }

    #BTNVisiteVirtuelle{
        font-size: 1.5vh;
        width: 32vw;
        border: 0.13vw white solid;
        border-radius: 10px;
        padding: 0.4vh;
    }

    #logo{
        z-index: 4;
        bottom:0.1vh;
        left: 50%;
        transform: translateX(-50%);
        grid-gap: 3vw;
    }

    #CentraleMarseille_white{
        height: 6vh;

    }
}
