

@media only screen and (min-width: 768px) {
    /* tablettes et ordinateurs */
    #container{
        height: 100%;
        background-position: center;
        background-size: cover;
        margin:0;
    }
    .MenuLogo{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 4vh;
        left: 4vw;
    }
    .NavBar{
        height: 70px;
        position: relative;
        place-items: center;
        border-bottom:0.7px solid #e5e5e5;
    }
    .pageViewContainer{
        display: grid;
        grid-template-columns: 10vw 80vw 10vw;
        height: calc(100% - 85px - 60px);
    }

    #ColumnRight{
        height: calc(100vh - 85px - 60px);
        align-items: center;
        display: flex;
    }

    #ColumnLeft{
        height: calc(100vh - 85px - 60px);
        align-items: center;
        display: flex;
    }

    a.ScrollButton{
        position: relative;
        height: 5vh;
        width: 100%;
        top:-10vh;
        font-family: "Briberra", cursive;
        background-repeat: no-repeat;
        background-size: contain;
        color: black;
        font-size: 2.3vw;
        text-decoration: none;
        margin-right: 1vw;
        margin-left: 1vw;
    }
    .titleLeft{
        margin: 0 0 0 2.5vw;
        position: absolute;
        top: 50%; /* poussé de la moitié de hauteur du référent */
        transform: translateY(-50%)
    }
    .titleRight{
        margin: 0 2.5vw 0 0;
        right: 0;
        transform: translateX(100%);
        position: absolute;
        top: 50%; /* poussé de la moitié de hauteur du référent */
        transform: translateY(-50%)
    }

    #BottomButtons{
        display: none;
    }

    .ScrollButtonLeft{
        background-image: url("../../Assets/Buttons/Buttons__scroll_sp_left.png");
        background-position: left;
    }

    .ScrollButtonRight {
        background-image: url("../../Assets/Buttons/Buttons__scroll_sp_right.png");
        background-position: right;
    }

}

@media only screen and (max-width: 767px) {
    /* smartphone */
    .NavBar{
        height: 6vh;
        position: relative;
        place-items: center;
        border-bottom:0.7px solid #e5e5e5;
    }

    .pageViewContainer{
        display: block;
        width: 100%;
        height: 80vh;
    }

    #ColumnRight{
        display: none;
    }

    #ColumnLeft{
        display: none;
    }
}

.container{
    height: 100%;
    background-position: center;
    background-size: cover;
}

.MenuButtonOpen{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 3vh;
    width: 3vh;
    right: 5vw;
}

.MenuButtonClose{
    top:5vh;
    margin-bottom: auto;
    width: 3vh;
    height: 3vh;
    position: absolute;
    left: 85vw;
}

.LogoMenuButtonClose{
    top:3vh;
    width: 6vh;
    height: 6vh;
    position: absolute;
    left: 10vw;
}


.Mouette{
    position : absolute;
    height: 60px;
    width: auto;
    left: 93%;
    top: 10px
}
/*Mouette de la barre de navigation pour mobile*/
.MouetteNavV{
    position: absolute;
    bottom: 0;
    height: 100%;
    left: 4vw;
}

.Menu {
    display: grid;
    grid-template-columns: repeat(6, 200px);
    grid-auto-rows: 70px;
    justify-content: end;
    padding-right: 9%;
}

.MenuItem{
    background-size: contain;
    font-size: 15px;
    font-family: "IBM Plex Sans Medium", cursive;
    position: relative;
    height: 100%;
}

.titre_menu{
    padding:5px;
    height: 5px;
    backgroundColor: black,
}

.circle{
    top: 5px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #52DEFF;
    position: relative;
}
.visited{
    height: 70px;
}

.MenuItem a{
    display: block;
    color: black;
    text-decoration: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.MenuV {
    display: grid;
    grid-template-columns: repeat(1, 100vw);
    justify-content: left;
    grid-auto-rows: 14vh;
    margin-top: 8vh;
    margin-left: 0;
}


.MenuItemV{
    background-size: contain;
    font-size: 20px;
    font-family: 'IBM Plex Sans Medium';
    position: relative;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    width: 80vw;
    border-bottom: 0.7px solid #e5e5e5;
    margin-top: 2vh;
}


.MenuItemV a{
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    color: black;
    text-decoration: none;

}

.MouetteV{
    position : absolute;
    height: 60px;
    width: auto;
    left: 80%;
    bottom: 0px
}

