#myPixiDiv{
    position: absolute;
    bottom:0;
}


@media only screen and (min-width: 768px) {

    #grounge{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100vw;
        opacity: 0.4;
    }
    canvas {
        display: flex;
        justify-content: center;
        width: 100vw;
        height: 17vh;
    }
}

@media only screen and (max-width: 767px) {
    #grounge{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100vw;
        height: 11vh;
        opacity: 0.6;
    }
    canvas {
        display: flex;
        justify-content: center;
        width: 100vw;
        height: 11vh;
        position: absolute;
        bottom: 0;

    }
}

