.SocialNetworksLink-container1{
    cursor: pointer;
    display: grid;
    justify-content: center;
    align-items: center;
    justify-items: center;
    align-content: center;
    position: relative;
}



.SocialNetworkButton{
    cursor: pointer;
    height: 2vh;
}

.LinksContainer{
    display: none;
    justify-content: space-between;
    background-color: white;
    box-shadow: 1px 1px 5px #b3b3b3;
    border-radius: 30px;
    padding: 0.5vmax;
    align-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 15;
}

.SocialNetworkIcon{
    cursor: pointer;
    height: 4vh;
    margin-bottom: 5px;
    margin-top: 5px;
    width: 4vh;
}

.closeArrowSocialNetworks{
    cursor: pointer;
}

@media only screen and (max-width: 767px) {
    .SocialNetworkButton{
        height: max-content;
    }

    .SocialNetworksLink-container1{
        grid-template:
                "a" auto
                "b" auto;
    }

    .LinksContainer{
        grid-area: a;
        flex-direction: column-reverse;
    }

    .closeArrowSocialNetworks{
        transform: rotate(-90deg);
        height: 3vh;
        margin-left: auto;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-right: auto;
    }

    .LinksContainerItems{
        padding-bottom:0;
        padding-top: 0;
    }
}

@media only screen and (min-width: 768px) {
    .SocialNetworkButton{
        height: max-content;
    }
    .SocialNetworksLink-container1{
        grid-template:
                "a" auto
                "b" auto;
    }

    .LinksContainerItems{
        padding-left: 0.5vmax;
        padding-right: 0.5vmax;
    }


    .LinksContainer{
        grid-area: b;
        left:1vh;
        vertical-align: middle;
        padding:0.2vmax
    }


    .closeArrowSocialNetworks{
        height: 3vh;
        margin-left: auto;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-right: auto;
    }
}
