
@font-face {
  font-family: Briberra;
  src: url("./Assets/Police/Briberra.ttf");
}

@font-face {
  font-family: "IBM_Bold";
  src: url("./Assets/Police/IBMPlexSans-Bold.ttf");
}

@font-face {
  font-family: "IBM_Light";
  src: url("./Assets/Police/IBMPlexSans-Light.ttf");
}

@font-face {
  font-family: "IBM_Medium";
  src: url("./Assets/Police/IBMPlexSans-Medium.ttf");
}

.App {
  text-align: center;
  overflow: hidden;
  background-color: white;
  background-position: center;
  background-size: cover;
}
h1{
  font-weight: normal;
}

.page{
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: white;
  background-position: center;
  background-size: cover;
}
