#pBaliseWM
{
    font-family: "IBM_Light", sans-serif;
    font-size : 0.5vw;
    text-align: justify;
    color: black;
}

@keyframes clignote {
    50% { opacity: 0; }
}

#BTNTeaserAlpha{
    text-decoration: none;
    font-family: Briberra, "Comic Sans MS";
    letter-spacing: 0.08vw;
    color: white;
    width: max-content;
    background-color: #0cf;

}


@supports ((-webkit-backdrop-filter: blur(2em)) or (backdrop-filter: blur(2em))) {
    #pBaliseWM{
        color: black;
        width: 29vw
    }
}


@media only screen and (max-width: 767px)  {
    #BTNTeaserAlpha{
        font-size: 2.4vmax;
        padding: 1vh;
        border-radius: 11px;
        box-shadow: 1px 1px 5px #e5e5e5;
        position: relative;
        top:2vh
    }

    .welcomingMessage{
        width: 70vw;
        margin:auto;
        margin-top: 1vh;
        position:relative;
        top:-2vh
    }
    .titleWM{
        font-size: 3.2vh;
        width:max-content ;
        text-align: left;
        font-family: "IBM_Medium", "Comic Sans MS";
        margin:0;
        grid-area: b;

    }

    .titleWMGuillemets{
       display: grid;
        grid-template: "a"
        "b";
        grid-template-columns: max-content max-content ;
    }

    #guillemets{
        grid-area: a;
        width: 14vw;
        justify-self: right;
        position: relative;
        animation: clignote 1s normal 2;
        animation-delay : 1s;
        left: 27px;
        top:10px
    }
    #pBaliseWM
    {
        font-size: 1.8vh;
        position: relative;
        margin-top: 2vh;
        margin-bottom: 3vh;
        width: 70vw;
    }}


@media only screen and (min-width: 768px) {
    #BTNTeaserAlpha{
        font-size: 1.3vmax;
        padding: 1vh;
        border-radius: 15px;
        box-shadow: 2px 2px 7px #e5e5e5;
    }

    #BTNTeaserAlpha:hover{
        transform: scale(1.1);
        transition: transform 0.5s;
    }

    .welcomingMessage{
        width: 65vw;
        margin-top: 1vh;

    }
    .titleWM{
        font-size: 3vw;
        width: 29vw ;
        text-align: left;
        justify-content: flex-end;
        font-family: "IBM_Medium", "Comic Sans MS";
        margin:0;

    }

    .titleWMGuillemets{
        display: inline-grid;
        grid-template-columns: auto auto ;

    }

    #guillemets{
        width: 4.5vw;
        position: relative;
        left: -2.8vw;
        top:-1.4vh;
        animation: clignote 1s normal 2;
        animation-delay : 1s;
    }
    .welcomingMessage{
        position: relative;
        top: 50%;
        transform:translateY(-50%);
        display: grid;
        width: fit-content;
        justify-content: left;
    }

    #pBaliseWM
    {
        margin-bottom: 5vh;
        margin-top: 5vh;
        font-size: 1.2vmax;
    }

}