
#pole_sport
{
    grid-area: a;
}

#pole_business
{
    grid-area: b;
}

#pole_VDE
{
    grid-area: c;
}

#pole_culture
{
    grid-area: d;
}

#pole_innovation
{
    grid-area: e;
}

#pole_citoyennete
{
    grid-area: f;
}


#Asso_UA
{
    display: none;
    border-radius: 10vmax;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0 0 1vmax black;
}

#AlphaButtons_container
{
    display: grid;
    grid-gap: 2vmax;
    justify-items: center;
    align-items: center;
    justify-content: center;
}

#AlphaButtons_container .PopUpBTN{
    cursor: pointer;
}

@media only screen and (max-width: 767px) {
    #AlphaButtons_container {
        grid-template:
                "a b" auto
                "c d" auto
                "e f" auto;
        grid-column-gap: 17vw;
        align-items: flex-start;
        margin-top: 6vh;
    }

    .Buttons
    {
        border-radius: 2vmax;
    }

    #Asso_UA .PopUpBTN
    {
        height: 13vh;
    }
}

@media only screen and (min-width: 768px) {
    #AlphaButtons_container {
        width: 50vw;
        grid-template-areas:" a b c"
                " d e f "
                " g h i ";
        grid-column-gap: 5vw;
        grid-row-gap: 1vh;

    }

    .Buttons
    {
        border-radius: 1vmax;
    }

    #Asso_UA .PopUpBTN
    {
        height: 15vh;
    }
}

