
@media only screen and (max-width: 767px) {
    #container
    {
        height: 80vh;
        position: relative;
    }
    #Logo
    {
        margin: 2vh auto 0 auto;
        position: relative;
        top: 0;
        width: 70vw;
    }
}

@media only screen and (min-width: 768px) {
    #container
    {
        margin-top: 4.5vh;
        height: 60vh;
        position: relative;
        margin-left: auto;
        margin-right: auto;
    }
    #Logo
    {
        float: left;
        position: relative;
        top: 50%;
        transform:translateY(-50%);
        margin: 0 auto;
        width: 25vw;
        margin-right: 7vw;
    }
}




