
@media only screen and (max-width: 767px)  {
    #Formulaire_Blue
    {
        display: block;
        border: none;
        border-radius: 10vmax;
        font-family: Briberra, cursive;
        letter-spacing: 0.3vw;
        font-size: 2.3vh;
        background-color: #00CCFF;
        color : white;
        box-shadow: 2px 2px 5px #e7e7e7;
        margin: 0;
        padding: 0.9vh;
        height: fit-content;
        width: fit-content;
        text-decoration: none;
        max-height: 20vh;
    }
    #Formulaire_White
    {

        display: block;
        border: none;
        border-radius: 1vmax;
        font-family: Briberra, cursive;
        letter-spacing: 0.3vw;
        font-size: 2.3vh;
        color : white;
        margin-left: 12vw;
        padding: 0.2vh;
        height: fit-content;
        width: 35vw;
        text-decoration: none;
        border:2px white solid;
    }
}

@media only screen and (min-width: 768px) {
    #Formulaire_Blue
    {
        display: block;
        border: none;
        border-radius: 1.5vmax;
        font-family: Briberra, cursive;
        letter-spacing: 0.08vw;
        font-size: 1.4vw;
        color : white;
        background-color: #00CCFF;
        box-shadow: 2px 2px 15px #d2d2d2;
        margin: 0;
        padding: 1.4vh;
        height: fit-content;
        width: fit-content;
        text-decoration: none;
    }
    #Formulaire_White
    {
        display: block;
        border-radius: 1.5vmax;
        font-family: Briberra, cursive;
        letter-spacing: 0.08vw;
        font-size: 1.2vw;
        color : white;
        margin-left: 2.5vw;
        padding: 0.8vh;
        height: fit-content;
        width: 13vw;
        text-decoration: none;
        border: 2px white solid;
    }
    #Formulaire_Blue:hover
    {
        box-shadow: 0 0 0.5vmax #225465;
        cursor: pointer;
    }
    @supports ((-webkit-backdrop-filter: blur(2em)) or (backdrop-filter: blur(2em))) {
        #Formulaire_Blue:hover
        {
            box-shadow: 0 0 0.5vmax white;
            cursor: pointer;
        }
    }

    #Formulaire_White:hover
    {
        cursor: pointer;
        color: white;
        box-shadow: 0px 0px 4px #ffffff;
    }

}
