.side-drawer {
    height: 100%;
    background-color: white;
    position: fixed;
    top: 0;
    right: 0;
    color: white;
    width: 100%;
    z-index: 200;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    transform: translateX(100%);
    transition: transform 0.3s ease-out;
}.side-drawer.open {
     transform: translateX(0);
 }
